(function() {
  'use strict';

  angular.module('angularJsonapiExample', [
    'ui.router',
    'angular-jsonapi',
    'angular-jsonapi-local',
    'angular-jsonapi-rest',
    'jsonFormatter',
    'ngClipboard',
    'promise-button',
    'RecursionHelper'
  ])
  .config(function(ngClipProvider) {
    ngClipProvider.setPath('bower_components/zeroclipboard/dist/ZeroClipboard.swf');
  })
  .config(function($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('frame', {
        url: '',
        templateUrl: 'app/frame/frame.html',
        controller: 'FrameController',
        abstract: true
      })
      .state('frame.hello', {
        url: '',
        templateUrl: 'app/frame/hello.html'
      })
      .state('frame.request', {
        url: '/{type}',
        template: '<ui-view></ui-view>',
        controller: 'RequestController',
        abstract: true,
        resolve: {
          factory: function($jsonapi, $stateParams) {
            return $jsonapi.getResource($stateParams.type);
          }
        }
      })
      .state('frame.request.all', {
        url: '',
        templateUrl: 'app/request/all.html',
        controller: 'RequestAllController',
        resolve: {
          collection: function(factory, $location, $jsonapi) {
            var params = $jsonapi.sourceRest.decodeParams($location.search());

            return factory.all(params);
          }
        }
      })
      .state('frame.request.get', {
        url: '/{id}',
        templateUrl: 'app/request/get.html',
        controller: 'RequestGetController',
        resolve: {
          object: function(factory, $stateParams) {
            return factory.get($stateParams.id);
          }
        }
      });

    $urlRouterProvider.otherwise('/robots');
  });
})();

(function() {
  'use strict';

  angular.module('angularJsonapiExample')
    .directive('angularJsonapiSearch', search);

  function search() {
    return {
      restrict: 'E',
      template: '<div ng-include="contentUrl"></div>',
      controller: controller,
      link: link,
      scope: {
        object: '=',
        key: '='
      }
    };

    function link(scope) {
      if (scope.schema.polymorphic === true) {
        scope.contentUrl = 'app/components/search/search-polymorphic.html';
      } else {
        scope.contentUrl = 'app/components/search/search.html';
      }
    }

    function controller($scope, $jsonapi, $timeout) {
      $scope.schema = $scope.object.schema.relationships[$scope.key];
      if ($scope.schema.polymorphic) {
        $scope.collections = {};
        angular.forEach($jsonapi.allResources(), function(resource, resourceName) {
          $scope.collections[resourceName] = resource.cache.index();
        });
      } else {
        $scope.model = $scope.schema.model;
        $scope.collection = $jsonapi.getResource($scope.model).cache.index();
      }

      $scope.show = false;
      $scope.isEmpty = isEmpty;
      $scope.addLink = addLink;
      $scope.showResults = showResults;
      $scope.hideResults = hideResults;
      $scope.setInput = setInput;
      $scope.getIndex = getIndex;

      function isEmpty(obj) {
        return Object.keys(obj).length === 0;
      }

      function addLink(target) {
        $scope.loading = true;
        $scope.object.link($scope.key, target).then(resolve, reject);
        $scope.show = false;

        function resolve() {
          $scope.loading = false;
          $scope.input = '';
        }

        function reject(error) {
          $scope.loading = false;
          $scope.error = true;
          $scope.errorText = error[0].statusText;
        }
      }

      function showResults() {
        $scope.error = false;
        $scope.show = true;
      }

      function hideResults() {
        $scope.show = false;
      }

      function setInput(value) {
        $scope.input = value;
      }

      function getIndex(modelName) {
        $timeout(function() {
          $scope.collection = $jsonapi.getResource(modelName).all().data;
        });
      }
    }
  }

})();

(function() {
  'use strict';

  angular.module('angularJsonapiExample')
    .filter('angularJsonapiSearchObject', searchFilter);

  function searchFilter($jsonapi) {
    var names = $jsonapi.listResources();

    return function(items, search, relationship, polymorphic) {
      if(items === undefined) {
        return;
      }

      if (!search) {
        if (polymorphic === true) {
          return [];
        } else if (angular.isArray(relationship)) {
          return items.filter(function(value) {
            return relationship.indexOf(value) === -1;
          });
        } else {
          return items;
        }
      }

      var results = [];
      var words = search.split(' ');
      var searchWord;

      if (polymorphic === false) {
        searchWord = search;
        angular.forEach(items, function(value) {
          if (relationship.indexOf(value) === -1 && value.toString().toLowerCase().indexOf(searchWord.toLowerCase()) > -1) {
            results.push(value);
          }
        });
      } else {
        if (words.length > 1) {
          searchWord = words.splice(1).join(' ');
          angular.forEach(items, function(value) {
            if (relationship.indexOf(value) === -1 && value.toString().toLowerCase().indexOf(searchWord.toLowerCase()) > -1) {
              results.push(value);
            }
          });
        } else if (names.indexOf(words[0]) > -1) {
          if (angular.isArray(relationship)) {
            return items.filter(function(value) {
              return relationship.indexOf(value) === -1;
            });
          } else {
            return items.filter(function(value) {
              return relationship !== value;
            });
          }
        }
      }

      return results;
    };
  }

})();

(function() {
  'use strict';

  angular.module('angularJsonapiExample')
    .filter('angularJsonapiSearchCollection', searchFilter);

  function searchFilter() {
    return function(items, search) {
      if (!search) {
        return items;
      }

      var results = {};
      var words = search.split(' ');
      angular.forEach(items, function(value, key) {
        if (key.indexOf(words[0]) > -1) {
          results[key] = value;
        }
      });

      return results;
    };
  }

})();

(function() {
  'use strict';

  angular.module('angularJsonapiExample')
    .constant('toTitleCase', toTitleCase)
    .filter('toTitleCase', toTitleCaseFilter);

  function toTitleCase(string) {
    var out = string.replace(/^\s*/, '');  // strip leading spaces
    out = out.replace(/^[a-z]|[^\s][A-Z]/g, function(str, offset) {
      if (offset === 0) {
        return str.toUpperCase();
      } else {
        return str.substr(0, 1) + ' ' + str.substr(1).toUpperCase();
      }
    });

    return out;
  }

  function toTitleCaseFilter() {
    return function(input) {
      return toTitleCase(input);
    };
  }
})();

