(function() {
  'use strict';

  angular.module('promise-button')
    .directive('promiseButton', promiseButton);

  function promiseButton($parse, $q) {
    return {
      restrict: 'A',
      priority: -1,
      compile: compile
    };

    function compile($element, attr) {
      var loadingClass = attr.loadingClass || 'loading';
      var errorClass = attr.errorClass || 'negative';
      var successClass = attr.successClass || 'positive';

      var fn = $parse(attr.ngClick, null,  true);
      return function ngEventHandler(scope, element) {
        element.on('click', onClick);

        function onClick(event) {
          event.preventDefault();
          event.stopImmediatePropagation();
          element.off('click');

          var callback = function() {
            element.addClass(loadingClass);
            element.removeClass(errorClass);
            element.removeClass(successClass);
            $q.resolve(fn(scope, {$event:event})).then(resolve, reject);
          };

          function resolve(response) {
            element.removeClass(loadingClass);
            element.addClass(successClass);
            element.on('click', onClick);

            return response;
          }

          function reject(response) {
            element.removeClass(loadingClass);
            element.addClass(errorClass);
            element.on('click', onClick);

            return response;
          }

          scope.$apply(callback);
        }
      };
    }
  }
})();

(function() {
  'use strict';

  angular.module('angularJsonapiExample')
    .directive('angularJsonapiObject', object);

  function object(RecursionHelper) {
    return {
      restrict: 'E',
      templateUrl: 'app/components/object/object.html',
      scope: {
        object: '=data',
        unlink: '&',
        nested: '='
      },
      require: '^angularJsonapiObject',
      compile: RecursionHelper.compile,
      controller: controller
    };

    function controller($scope, $interval) {
      var interval;

      $scope.showMore = false;
      $scope.isArray = angular.isArray;

      $scope.$watch('showMore', toggleTimmer);

      $scope.$on('close', function() {
        $scope.showMore = false;
      });

      $scope.equals = angular.equals;

      function toggleTimmer(value) {
        if (value === true) {
          $scope.updateDiff = (Date.now() - $scope.object.updatedAt) / 1000;
          interval = $interval(function() {
            $scope.updateDiff = (Date.now() - $scope.object.updatedAt) / 1000;
          }, 100);
        } else if (value === false) {
          $interval.cancel(interval);
        }
      }
    }
  }

})();

(function() {
  'use strict';

  angular.module('angularJsonapiExample')
    .directive('angularJsonapiObjectRelationships', objectRelationships);

  function objectRelationships(RecursionHelper) {
    return {
      restrict: 'E',
      templateUrl: 'app/components/object/object-relationships.html',
      scope: {
        object: '=data'
      },
      compile: RecursionHelper.compile,
      controller: controller
    };

    function controller($scope) {
      $scope.isArray = angular.isArray;
      $scope.emptyRelationship = emptyRelationship;
      $scope.form = $scope.object.parent !== undefined;

      function emptyRelationship(relationship) {
        return relationship === undefined ||
          relationship === null ||
          angular.isArray(relationship) &&
          relationship.length === 0;
      }
    }
  }

})();

